"use client"

import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"
import { QrCode, Shield } from "lucide-react"

interface TwoFactorAuthDialogProps {
  onSetup: (code: string) => Promise<boolean>
  onGetQRCode: () => Promise<string>
}

export function TwoFactorAuthDialog({ onSetup, onGetQRCode }: TwoFactorAuthDialogProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [qrCodeUrl, setQrCodeUrl] = useState<string>("")
  const [verificationCode, setVerificationCode] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState("")
  const [step, setStep] = useState<"initial" | "verify">("initial")

  const handleGetQRCode = async () => {
    try {
      setIsLoading(true)
      setError("")
      const qrCode = await onGetQRCode()
      setQrCodeUrl(qrCode)
      setStep("verify")
    } catch (err) {
      setError("Failed to generate QR code. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  const handleVerify = async () => {
    try {
      setIsLoading(true)
      setError("")
      const success = await onSetup(verificationCode)
      if (success) {
        setIsOpen(false)
        setStep("initial")
        setVerificationCode("")
        setQrCodeUrl("")
      } else {
        setError("Invalid verification code. Please try again.")
      }
    } catch (err) {
      setError("Verification failed. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button variant="outline" className="w-full">
          <Shield className="mr-2 h-4 w-4" />
          Setup Two-Factor Authentication
        </Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle>Two-Factor Authentication Setup</DialogTitle>
          <DialogDescription>
            Enhance your account security by enabling two-factor authentication.
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-4 py-4">
          {step === "initial" ? (
            <div className="space-y-4">
              <p className="text-sm text-gray-500">
                Two-factor authentication adds an extra layer of security to your
                account. Once enabled, you'll need to enter a verification code
                from your authenticator app when accessing your passwords.
              </p>
              <Button
                onClick={handleGetQRCode}
                className="w-full"
                disabled={isLoading}
              >
                <QrCode className="mr-2 h-4 w-4" />
                Generate QR Code
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {qrCodeUrl && (
                <Card className="p-4">
                  <div className="flex justify-center">
                    <img
                      src={qrCodeUrl}
                      alt="QR Code"
                      className="w-48 h-48"
                    />
                  </div>
                  <p className="text-sm text-center mt-2 text-gray-500">
                    Scan this QR code with your authenticator app
                  </p>
                </Card>
              )}

              <div className="space-y-2">
                <Label htmlFor="verificationCode">Verification Code</Label>
                <Input
                  id="verificationCode"
                  placeholder="Enter 6-digit code"
                  value={verificationCode}
                  onChange={(e) => setVerificationCode(e.target.value)}
                  maxLength={6}
                />
              </div>

              {error && (
                <p className="text-sm text-red-500">{error}</p>
              )}

              <div className="flex space-x-2">
                <Button
                  variant="outline"
                  onClick={() => setStep("initial")}
                  disabled={isLoading}
                  className="flex-1"
                >
                  Back
                </Button>
                <Button
                  onClick={handleVerify}
                  disabled={isLoading || verificationCode.length !== 6}
                  className="flex-1"
                >
                  Verify
                </Button>
              </div>
            </div>
          )}
        </div>
      </DialogContent>
    </Dialog>
  )
}
