import { randomBytes, createCipheriv, createDecipheriv } from 'crypto';

const ALGORITHM = 'aes-256-gcm';
const IV_LENGTH = 16;
const SALT_LENGTH = 64;
const TAG_LENGTH = 16;
const KEY_LENGTH = 32;

export function generateKey(password: string): Buffer {
  const salt = randomBytes(SALT_LENGTH);
  return Buffer.concat([salt, Buffer.from(password)]);
}

export function encrypt(text: string, key: Buffer): string {
  try {
    const iv = randomBytes(IV_LENGTH);
    const cipher = createCipheriv(ALGORITHM, key.slice(0, KEY_LENGTH), iv);
    
    let encrypted = cipher.update(text, 'utf8', 'hex');
    encrypted += cipher.final('hex');
    
    const tag = cipher.getAuthTag();
    const result = iv.toString('hex') + encrypted + tag.toString('hex');
    return result;
  } catch (error) {
    throw new Error('Encryption failed');
  }
}

export function decrypt(encryptedData: string, key: Buffer): string {
  try {
    const iv = Buffer.from(encryptedData.slice(0, IV_LENGTH * 2), 'hex');
    const tag = Buffer.from(encryptedData.slice(-TAG_LENGTH * 2), 'hex');
    const encrypted = encryptedData.slice(IV_LENGTH * 2, -TAG_LENGTH * 2);
    
    const decipher = createDecipheriv(ALGORITHM, key.slice(0, KEY_LENGTH), iv);
    decipher.setAuthTag(tag);
    
    let decrypted = decipher.update(encrypted, 'hex', 'utf8');
    decrypted += decipher.final('utf8');
    
    return decrypted;
  } catch (error) {
    throw new Error('Decryption failed');
  }
}
