"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Eye, EyeOff, Trash2, Copy } from "lucide-react"
import { decrypt } from "@/lib/encryption"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

interface Password {
  id: string
  website: string
  username: string
  password: string
}

interface PasswordListProps {
  passwords: Password[]
  onDelete: (id: string) => void
  encryptionKey: Buffer | null
}

export function PasswordList({ passwords, onDelete, encryptionKey }: PasswordListProps) {
  const [visiblePasswords, setVisiblePasswords] = useState<Record<string, string>>({})

  const togglePasswordVisibility = (id: string, encryptedPassword: string) => {
    setVisiblePasswords((prev) => {
      if (prev[id]) {
        const { [id]: _, ...rest } = prev
        return rest
      } else {
        if (!encryptionKey) {
          console.error("No encryption key available")
          return prev
        }
        
        try {
          const decryptedPassword = decrypt(encryptedPassword, encryptionKey)
          return { ...prev, [id]: decryptedPassword }
        } catch (error) {
          console.error("Failed to decrypt password:", error)
          return prev
        }
      }
    })
  }

  const copyToClipboard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text)
    } catch (err) {
      console.error("Failed to copy text:", err)
    }
  }

  const getPasswordForCopy = (id: string, encryptedPassword: string) => {
    if (visiblePasswords[id]) {
      return visiblePasswords[id]
    }
    
    if (!encryptionKey) {
      return encryptedPassword
    }
    
    try {
      return decrypt(encryptedPassword, encryptionKey)
    } catch (error) {
      console.error("Failed to decrypt password for copy:", error)
      return encryptedPassword
    }
  }

  if (passwords.length === 0) {
    return (
      <Card className="p-6">
        <p className="text-center text-gray-500">No passwords stored yet.</p>
      </Card>
    )
  }

  return (
    <Card className="w-full overflow-hidden">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Website</TableHead>
            <TableHead>Username</TableHead>
            <TableHead>Password</TableHead>
            <TableHead className="w-[100px]">Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {passwords.map((entry) => (
            <TableRow key={entry.id}>
              <TableCell className="font-medium">{entry.website}</TableCell>
              <TableCell>
                <div className="flex items-center space-x-2">
                  <span>{entry.username}</span>
                  <Button
                    variant="ghost"
                    size="icon"
                    onClick={() => copyToClipboard(entry.username)}
                  >
                    <Copy size={16} />
                  </Button>
                </div>
              </TableCell>
              <TableCell>
                <div className="flex items-center space-x-2">
                  <span>
                    {visiblePasswords[entry.id] || "••••••••"}
                  </span>
                  <Button
                    variant="ghost"
                    size="icon"
                    onClick={() => togglePasswordVisibility(entry.id, entry.password)}
                  >
                    {visiblePasswords[entry.id] ? (
                      <EyeOff size={16} />
                    ) : (
                      <Eye size={16} />
                    )}
                  </Button>
                  <Button
                    variant="ghost"
                    size="icon"
                    onClick={() => copyToClipboard(getPasswordForCopy(entry.id, entry.password))}
                  >
                    <Copy size={16} />
                  </Button>
                </div>
              </TableCell>
              <TableCell>
                <Button
                  variant="ghost"
                  size="icon"
                  onClick={() => onDelete(entry.id)}
                  className="text-red-500 hover:text-red-700"
                >
                  <Trash2 size={16} />
                </Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Card>
  )
}
