"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"
import { Switch } from "@/components/ui/switch"
import { PasswordStrengthMeter } from "./PasswordStrengthMeter"
import { generatePassword } from "@/lib/passwordGenerator"
import { Eye, EyeOff, RefreshCw } from "lucide-react"

interface PasswordFormProps {
  onSubmit: (data: { website: string; username: string; password: string }) => void;
}

export function PasswordForm({ onSubmit }: PasswordFormProps) {
  const [website, setWebsite] = useState("")
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [options, setOptions] = useState({
    length: 16,
    includeSymbols: true,
    includeNumbers: true,
    includeUpperCase: true,
    includeLowerCase: true,
  })

  const handleGeneratePassword = () => {
    try {
      const newPassword = generatePassword(options)
      setPassword(newPassword)
    } catch (error) {
      console.error("Failed to generate password:", error)
    }
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit({ website, username, password })
    setWebsite("")
    setUsername("")
    setPassword("")
  }

  return (
    <Card className="p-6 w-full max-w-md mx-auto">
      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="space-y-2">
          <Label htmlFor="website">Website</Label>
          <Input
            id="website"
            placeholder="example.com"
            value={website}
            onChange={(e) => setWebsite(e.target.value)}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="username">Username</Label>
          <Input
            id="username"
            placeholder="johndoe@example.com"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>

        <div className="space-y-2">
          <Label htmlFor="password">Password</Label>
          <div className="flex space-x-2">
            <div className="relative flex-1">
              <Input
                id="password"
                type={showPassword ? "text" : "password"}
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                className="pr-10"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-1/2 -translate-y-1/2"
              >
                {showPassword ? <EyeOff size={20} /> : <Eye size={20} />}
              </button>
            </div>
            <Button
              type="button"
              variant="outline"
              onClick={handleGeneratePassword}
              className="flex-shrink-0"
            >
              <RefreshCw size={20} />
            </Button>
          </div>
          <PasswordStrengthMeter password={password} />
        </div>

        <div className="space-y-4">
          <Label>Password Generation Options</Label>
          <div className="space-y-2">
            <div className="flex items-center justify-between">
              <Label htmlFor="length">Length: {options.length}</Label>
              <Input
                id="length"
                type="range"
                min="8"
                max="32"
                value={options.length}
                onChange={(e) => setOptions({ ...options, length: parseInt(e.target.value) })}
                className="w-32"
              />
            </div>
            <div className="flex items-center justify-between">
              <Label htmlFor="symbols">Include Symbols</Label>
              <Switch
                id="symbols"
                checked={options.includeSymbols}
                onCheckedChange={(checked) => setOptions({ ...options, includeSymbols: checked })}
              />
            </div>
            <div className="flex items-center justify-between">
              <Label htmlFor="numbers">Include Numbers</Label>
              <Switch
                id="numbers"
                checked={options.includeNumbers}
                onCheckedChange={(checked) => setOptions({ ...options, includeNumbers: checked })}
              />
            </div>
            <div className="flex items-center justify-between">
              <Label htmlFor="uppercase">Include Uppercase</Label>
              <Switch
                id="uppercase"
                checked={options.includeUpperCase}
                onCheckedChange={(checked) => setOptions({ ...options, includeUpperCase: checked })}
              />
            </div>
            <div className="flex items-center justify-between">
              <Label htmlFor="lowercase">Include Lowercase</Label>
              <Switch
                id="lowercase"
                checked={options.includeLowerCase}
                onCheckedChange={(checked) => setOptions({ ...options, includeLowerCase: checked })}
              />
            </div>
          </div>
        </div>

        <Button type="submit" className="w-full">
          Save Password
        </Button>
      </form>
    </Card>
  )
}
