"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"
import { Lock, User } from "lucide-react"

interface LoginFormProps {
  onLogin: (email: string, masterPassword: string) => boolean
}

export function LoginForm({ onLogin }: LoginFormProps) {
  const [email, setEmail] = useState("")
  const [masterPassword, setMasterPassword] = useState("")
  const [error, setError] = useState("")
  const [isRegistering, setIsRegistering] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!email || !masterPassword) {
      setError("Please fill in all fields")
      return
    }

    if (masterPassword.length < 8) {
      setError("Master password must be at least 8 characters long")
      return
    }

    if (!email.includes("@")) {
      setError("Please enter a valid email address")
      return
    }
    
    setError("")
    const success = onLogin(email, masterPassword)
    
    if (!success) {
      setError("Invalid email or password. Please try again.")
    }
  }

  const toggleMode = () => {
    setIsRegistering(!isRegistering)
    setError("")
    setEmail("")
    setMasterPassword("")
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50">
      <Card className="w-full max-w-md p-8">
        <div className="text-center mb-8">
          <div className="flex justify-center mb-4">
            <Lock className="h-12 w-12 text-blue-600" />
          </div>
          <h1 className="text-2xl font-bold text-gray-900">
            Secure Password Manager
          </h1>
          <p className="text-gray-600 mt-2">
            {isRegistering 
              ? "Create your secure vault" 
              : "Sign in to access your passwords"
            }
          </p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-2">
            <Label htmlFor="email">Email</Label>
            <div className="relative">
              <User className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                id="email"
                type="email"
                placeholder="Enter your email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                className="pl-10"
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="masterPassword">Master Password</Label>
            <div className="relative">
              <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                id="masterPassword"
                type="password"
                placeholder="Enter your master password"
                value={masterPassword}
                onChange={(e) => setMasterPassword(e.target.value)}
                required
                className="pl-10"
              />
            </div>
          </div>

          {error && (
            <p className="text-sm text-red-500">{error}</p>
          )}

          <Button type="submit" className="w-full">
            {isRegistering ? "Create Vault" : "Unlock Password Manager"}
          </Button>
        </form>

        <div className="mt-6 text-center">
          <button
            type="button"
            onClick={toggleMode}
            className="text-sm text-blue-600 hover:text-blue-800"
          >
            {isRegistering 
              ? "Already have an account? Sign in" 
              : "First time? Create your vault"
            }
          </button>
        </div>
      </Card>
    </div>
  )
}
