"use client"

import { useState, useEffect } from "react"
import { PasswordForm } from "@/components/ui/PasswordForm"
import { PasswordList } from "@/components/ui/PasswordList"
import { TwoFactorAuthDialog } from "@/components/ui/TwoFactorAuthDialog"
import { LoginForm } from "@/components/ui/LoginForm"
import { Card } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { encrypt, decrypt, generateKey } from "@/lib/encryption"
import { LogOut } from "lucide-react"

interface StoredPassword {
  id: string
  website: string
  username: string
  password: string
}

interface UserAccount {
  email: string
  passwordHash: string
}

export default function PasswordManager() {
  const [passwords, setPasswords] = useState<StoredPassword[]>([])
  const [encryptionKey, setEncryptionKey] = useState<Buffer | null>(null)
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [currentUser, setCurrentUser] = useState<string>("")

  useEffect(() => {
    if (isLoggedIn && currentUser && encryptionKey) {
      const userPasswordsKey = `passwords_${currentUser}`
      const storedPasswords = localStorage.getItem(userPasswordsKey)
      if (storedPasswords) {
        try {
          const parsedPasswords = JSON.parse(storedPasswords)
          setPasswords(parsedPasswords)
        } catch (error) {
          console.error("Failed to load passwords:", error)
        }
      }
    }
  }, [isLoggedIn, currentUser, encryptionKey])

  const hashPassword = (password: string): string => {
    let hash = 0
    for (let i = 0; i < password.length; i++) {
      const char = password.charCodeAt(i)
      hash = ((hash << 5) - hash) + char
      hash = hash & hash
    }
    return hash.toString()
  }

  const handleLogin = (email: string, masterPassword: string): boolean => {
    const userKey = `user_${email}`
    const storedUser = localStorage.getItem(userKey)
    
    if (storedUser) {
      try {
        const userData: UserAccount = JSON.parse(storedUser)
        const inputPasswordHash = hashPassword(masterPassword)
        
        if (userData.passwordHash === inputPasswordHash) {
          const key = generateKey(masterPassword)
          setEncryptionKey(key)
          setCurrentUser(email)
          setIsLoggedIn(true)
          return true
        } else {
          return false
        }
      } catch (error) {
        console.error("Failed to parse user data:", error)
        return false
      }
    } else {
      const passwordHash = hashPassword(masterPassword)
      const newUser: UserAccount = {
        email,
        passwordHash
      }
      
      localStorage.setItem(userKey, JSON.stringify(newUser))
      
      const key = generateKey(masterPassword)
      setEncryptionKey(key)
      setCurrentUser(email)
      setIsLoggedIn(true)
      return true
    }
  }

  const handleLogout = () => {
    setIsLoggedIn(false)
    setCurrentUser("")
    setEncryptionKey(null)
    setPasswords([])
  }

  const handleSavePassword = ({ website, username, password }: { website: string; username: string; password: string }) => {
    if (!encryptionKey || !currentUser) return

    const encryptedPassword = encrypt(password, encryptionKey)
    const newPassword = {
      id: crypto.randomUUID(),
      website,
      username,
      password: encryptedPassword
    }

    const updatedPasswords = [...passwords, newPassword]
    setPasswords(updatedPasswords)
    
    const userPasswordsKey = `passwords_${currentUser}`
    localStorage.setItem(userPasswordsKey, JSON.stringify(updatedPasswords))
  }

  const handleDeletePassword = (id: string) => {
    const updatedPasswords = passwords.filter(p => p.id !== id)
    setPasswords(updatedPasswords)
    
    if (currentUser) {
      const userPasswordsKey = `passwords_${currentUser}`
      localStorage.setItem(userPasswordsKey, JSON.stringify(updatedPasswords))
    }
  }

  const handleSetup2FA = async (code: string) => {
    try {
      const response = await fetch("/api/2fa", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ token: code }),
      })

      const data = await response.json()
      return data.success
    } catch (error) {
      console.error("Failed to setup 2FA:", error)
      return false
    }
  }

  const handleGetQRCode = async () => {
    try {
      const response = await fetch("/api/2fa")
      const data = await response.json()
      return data.qrCodeUrl
    } catch (error) {
      console.error("Failed to get QR code:", error)
      throw error
    }
  }

  if (!isLoggedIn) {
    return <LoginForm onLogin={handleLogin} />
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <Card className="max-w-4xl mx-auto">
        <div className="p-6">
          <div className="flex justify-between items-center mb-8">
            <div>
              <h1 className="text-3xl font-bold">
                Secure Password Manager
              </h1>
              <p className="text-gray-600 mt-1">
                Welcome back, {currentUser}
              </p>
            </div>
            <Button
              variant="outline"
              onClick={handleLogout}
              className="flex items-center gap-2"
            >
              <LogOut size={16} />
              Logout
            </Button>
          </div>

          <div className="mb-6">
            <TwoFactorAuthDialog
              onSetup={handleSetup2FA}
              onGetQRCode={handleGetQRCode}
            />
          </div>

          <Tabs defaultValue="stored" className="w-full">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="stored">Stored Passwords</TabsTrigger>
              <TabsTrigger value="add">Add Password</TabsTrigger>
            </TabsList>
            <TabsContent value="stored" className="mt-6">
              <PasswordList
                passwords={passwords}
                onDelete={handleDeletePassword}
                encryptionKey={encryptionKey}
              />
            </TabsContent>
            <TabsContent value="add" className="mt-6">
              <PasswordForm onSubmit={handleSavePassword} />
            </TabsContent>
          </Tabs>
        </div>
      </Card>
    </div>
  )
}
